/* Restaurant-Menu ⭐⭐  */

.restaurant-summary {
  background-color:rgb(91, 108, 108);
  color: #fff;
}
.restaurant-img {
  width: 245px;
  height: 170px;
  border-radius: 10px;
  object-fit: cover;
}
.restaurant-summary-details {
  flex-basis: 520px;
}
.restaurant-title {
  font-size: 40px;
}
.restaurant-tags {
  color: inherit;
  font-size: 15px;
}
.restaurant-details {
  
  font-size: 16px;
  max-width: 340px;
}
.restaurant-rating {
  display: flex;
  align-items: center;
  padding: 5px 8px;
  gap: 5px;
  background-color: rgb(0,173,29);
  border-radius: 5px;
}

.restaurant-menu-content {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  
}

.menu-items-container {
  margin-top: 30px;
  width: -webkit-fill-available;
}
.menu-title-wrap {
  padding: 20px;
}
.item-title {
  width: 85%; 
  font-size: 1.08rem;
}

.item-desc {
 margin-top: 15px;
 color: rgba(40, 44, 63, 0.45);
 width: 72%;
 font-size: 1rem;
 
 /* max-width: 370px; */
}
.item-cost {
margin-top: 10px;
 font-size: .9rem;
}
.menu-count {
  margin-top: 14px;
  line-height: 1.3;
  color: #114232;
  letter-spacing: -0.3px;
  font-size: 1.1rem;
}

.menu-items-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  max-height: 250px;
  padding: 20px;
  border-bottom: rgba(40, 44, 63, 0.45) 0.5px solid;
}
.menu-item-details {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  overflow: hidden;
  width: 700px;
}
.menu-item-img {
  height: 100px;
  width: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.add-btn {
  background-color: #FDA403;
  color: black;
  font-size: 15px;
  font-weight: 600;
  padding: 8px 25px;
  cursor: pointer;
  margin-top: 10px;
  border-radius: 5px;
}
.add-btn .black {
  background-color: black;
  color: white;
}
.menu-img-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 150px;
}

